import React from "react";
import {
  ServicesContainer,
  ServicesH1,
  ServicesWrapper,
  ServicesCard,
  ServicesIcon,
  ServicesH2,
  ServicesP,
} from "./ServicesElements";
import Icon1 from "../../images/react.svg";
import Icon2 from "../../images/code.svg";
import Icon3 from "../../images/project.svg";

const Services = () => {
  return (
    <ServicesContainer id="experience">
      <ServicesH1>Experience</ServicesH1>
      <ServicesWrapper>
        <ServicesCard>
          <ServicesIcon src={Icon1} />
          <ServicesH2>Companies</ServicesH2>
          <ServicesP>Samsung SDS</ServicesP>
          <ServicesP>Grab</ServicesP>
          <ServicesP>Circles Life</ServicesP>
        </ServicesCard>
        <ServicesCard>
          <ServicesIcon src={Icon2} />
          <ServicesH2>Coding Languages</ServicesH2>
          <ServicesP>Python</ServicesP>
          <ServicesP>Javascript Frameworks (Reactjs, Nodejs)</ServicesP>
          <ServicesP>Golang</ServicesP>
          <ServicesP>Ruby</ServicesP>
        </ServicesCard>
        <ServicesCard>
          <ServicesIcon src={Icon3} />
          <ServicesH2>Latest Project</ServicesH2>
          <ServicesP>Migrating from Monolith to Microservices Architecture</ServicesP>
          <ServicesP>Involved in Development Sass Platform for B2B Telco Market</ServicesP>
        </ServicesCard>
      </ServicesWrapper>
    </ServicesContainer>
  );
};

export default Services;
